<template>
    <div>
    <ul class="list-group"> <!-- podemos usar Frutas o arrayordenado -->
        <li v-for="(item,index) of arrayOrdenado" :key="item.id" @click="aumentar(index)"
            class="list-group-item d-flex justify-content-between align-items-center">
            {{item.nombre}} / posicion: {{index}}
            <span class="badge badge-primary badge-pill">{{item.cantidad}}</span>
        </li>
    </ul>
    <button class="btn btn-danger btn-block" @click="reiniciar">Reinicar</button>
    </div>
</template>

<script>
import {mapState , mapMutations} from 'vuex'

export default {
    name:'listas',
    computed: {
        ...mapState(['frutas']),
        arrayOrdenado(){
            return  this.frutas.slice(0).sort( (a,b) => b.cantidad - a.cantidad)
        }
    },
    methods: {
        ...mapMutations(['aumentar' , 'reiniciar'])
    }
}
</script>